@import url("https://fonts.googleapis.com/css?family=Roboto");

/* Global Styles */
:root {
  --gh-pattern-1: #fff4f9;
  --gh-pattern-2: #ffebf1;
  --gh-pattern-3: #ffe3d7;
  --gh-pattern-4: #ffe2b1;
  --gh-pattern-5: #ffea8b;

  --gh-green-1: #b9f3cd;
  --gh-green-2: #96ed85;
  --gh-green-3: #73e79c;
  --gh-green-4: #51e183;
  --gh-green-5: #2edc6b;
  --gh-green-6: #21c058;
  --gh-green-7: #1b9d48;
  --gh-green-8: #157a38;
  --gh-green-9: #0f5728;
  --gh-green-10: #062310;

  --gh-blue-1: #c7eaf9;
  --gh-blue-2: #a2ddf6;
  --gh-blue-3: #7dcff2;
  --gh-blue-4: #58c1ee;
  --gh-blue-5: #33b4eb;
  --gh-blue-6: #16a3df;
  --gh-blue-7: #1287ba;
  --gh-blue-8: #0f6c95;
  --gh-blue-9: #0b4f6c;
  --gh-blue-10: #07364a;

  --gh-grey-1: #cccccc;
  --gh-grey-2: #adadad;
  --gh-grey-3: #999999;
  --gh-grey-4: #757575;
  --gh-grey-5: #666666;

  --gh-pink-1: #fff0f7;
  --gh-pink-2: #efa9ca;
  --gh-pink-3: #e986b4;
  --gh-pink-4: #e3649f;
  --gh-pink-5: #da3582;

  --gh-red-1: #e22452;
  --gh-red-2: #ce2727;
  --gh-red-3: #bc2424;

  --gh-yellow-1: #ffedc2;
  --gh-yellow-2: #f6e4be;
  --gh-yellow-3: #ffd470;
  --gh-yellow-4: #ffc847;

  --gh-orange-1: #ffbc1f;
  --gh-orange-2: #f5ab00;
  --gh-orange-3: #f9aa81;

  --gh-brown-1: #a37200;
  --gh-brown-2: #7a5600;
  --gh-brown-3: #523900;
  --gh-brown-4: #823d40;

  --transition: all 0.3s linear;
  --light-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
  --dark-shadow: 0 5px 15px rgba(0, 0, 0, 0.2);
  --max-width: 1170px;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: "Roboto", sans-serif;
  font-size: 2rem;
  line-height: 1.6;
  background-color: #29414E;
  color: #333;
}

ul {
  list-style-type: none;
}
a {
  text-decoration: none;
}
h1,
h2,
h3,
h4 {
  letter-spacing: var(--spacing);
  text-transform: capitalize;
  line-height: 1.25;
  margin-bottom: 0.75rem;
}
h1 {
  font-size: 3rem;
}
h2 {
  font-size: 2rem;
}
h3 {
  font-size: 1.25rem;
}
h4 {
  font-size: 0.875rem;
}
p {
  margin-bottom: 1.25rem;
}

input[type="text"],
input[type="number"] {
  border: 1px solid var(--gh-pink-2);
  border-radius: 2px;
  display: block;
  font-size: 1.2rem;
  padding: 5px;
  width: 100%;
}

button {
  cursor: pointer;
  box-shadow: var(--light-shadow);
  background-color: var(--gh-orange-3);
  color: #fff;
  border: 0;
  display: block;
  font-size: 18px;
  margin: 10px 0 30px;
  padding: 10px;
  width: 100%;
}
@media screen and (min-width: 800px) {
  h1 {
    font-size: 4rem;
  }
  h2 {
    font-size: 2.5rem;
  }
  h3 {
    font-size: 1.75rem;
  }
  h4 {
    font-size: 1rem;
  }
  body {
    font-size: 1rem;
  }
  h1,
  h2,
  h3,
  h4 {
    line-height: 1;
  }
}


/*general classes */

nav {
  margin-top: 20px;
}

.navbar-title {
  color: var(--gh-yellow-4);
  display: flex;
  align-items: center;
}

.navbar-title-text {
  letter-spacing: 2px;
  margin-left: 15px;
  font-size: 2.5rem;
}

.navbar-title-icon {
  font-size: 3.5rem;
  display: block;
}

.navbar-container {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  width: 90%;
  margin: 0 auto;
}

.navbar-links {
  display: flex;
  flex-direction: row;
}

.navbar-link {
  display: flex;
  flex-direction: row;
  align-items: center;
  margin: 0 30px;
}

.navbar-icons {
  color: var(--gh-yellow-4);
  font-size: 30px;
  color: #fff;
}


.navbar-option {
  margin: 0px 10px 3px 10px;
  font-size: 30px;
  color: var(--gh-yellow-4);
}

/*main container */
.main-container {
  margin: 0 auto;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: center;
  margin-top: 50px;
}

.image-grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: repeat (2, 1fr);
  grid-column-gap: 52px;
  grid-row-gap: 0px;
  margin-right: 50px;
}

@media screen and (max-width: 720px) {
  .image-grid {
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: repeat (2, 1fr);
  }

  .main-container {
    display: flex;
    flex-direction: column-reverse;
    align-items: center;
    justify-content: center;
  }
}
.image-button-pair {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.grid-image {
  width: 220px;
  height: 220px;
  border-radius: 10px;
}

.grid-image:hover {
  box-shadow: var(--dark-shadow);
}
.grid-button {
  width: 220px;
  height: 40px;
  border-radius: 5px;
  background-color: #FFA300;

}

.grid-button:hover {
  box-shadow: var(--dark-shadow);
}

/*main container right size */
.main-container-description {
  width: 380px;
}

.more-button {
  position:absolute;
  width: 140px;
  height: 60px;
  border-radius: 5px;
  margin: 50px auto;
  font-size: 22px;
  top: 800px;
}
.main-container-text {
  font-size: 20px;
  margin-top: 50px;
}

/*favorite container */
.favorite-container {
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
}
.image-button-pair {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}


.favorite-image-grid {
  margin-top: 10px;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: repeat (3, 1fr);
  grid-column-gap: 20px;
  grid-row-gap: 20px;
}

.fav-grid-image {
  width: 220px;
  height: 220px;
  border-radius: 10px;
}


.favorites-button {
  width: 200px;
  border-radius: 5px;
  background-color: #FFA300;
}

@media screen and (max-width: 720px) {
  .favorite-image-grid {
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: repeat(2, 1fr);
  }
}

.favorite-image-title {
  font-size: 3.5rem;
} 



/* Header Styles */
header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px;
  color: #fff;
}

/* Title Styles */
h1 {
  font-size: 36px;
  font-weight: bold;
  text-align: center;
  margin-top: 20px;
  color: #fff;
}

/* Subheading Styles */
p {
  font-size: 16px;
  text-align: center;
  color: #fff;
}

/* cat card styles*/
.cat-card {
  display: grid;
  grid-template-columns: repeat(3, 1fr); /* 3 columns */
  grid-template-rows: repeat(3, 1fr); /* 3 rows */
  width: 100%; /* Adjusted width */
  margin: 10px auto; /* Centered the card horizontally */
  padding: 10px;
  border-radius: 10px;
  text-align: center;
}

/* Cat Image Styles */
.cat-images {
  display: grid;
  grid-template-columns: repeat(3, 1fr); /* 3 columns */
  grid-template-rows: repeat(2, 1fr); /* 2 rows */
  grid-gap: 10px; /* Reduced gap between grid items */
  justify-items: center; /* Center grid items horizontally */
  margin-bottom: 20px; /* Added margin bottom for spacing */
  max-width: 1200px; /* Added max width */
  margin: 0 auto; /* Centered horizontally */
}

@media screen and (max-width: 720px) {
  .cat-images {
    grid-template-columns: repeat(2, 1fr); /* 2 columns for smaller screens */
    grid-template-rows: repeat(3, 1fr); /* 3 rows for smaller screens */
  }
}

/* Adjusted styles for grid images */
.cat-image {
  width: 220px; /* Adjusted width */
  height: 220px; /* Adjusted height */
  border-radius: 10px;
}

/* Adjusted styles for favorite buttons */
.fav-button {
  width: 60px;
  height: 40px;
  border-radius: 5px;
  background-color: #FFA300;
  color: #fff;
  border: none;
  cursor: pointer;
  margin-top: 5px; /* Reduced margin */
}

.fav-button:hover {
  box-shadow: var(--dark-shadow); /* Added shadow on hover */
}

/* More Button Styles */
.more-button {
  display: block;
  margin: 20px auto; /* Adjusted margin for closer spacing */
  padding: 10px 20px;
  background-color: #FFA300;
  color: #fff;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  width: 90px;
  max-width: 200px; /* Added max width */
}
